/* eslint-disable react/prop-types */
import React, { forwardRef, ReactNode, useEffect, useRef, useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import { CFormInput } from '../form/CFormInput'
import { CFormSelect } from '../form/CFormSelect'
import { CInputGroup } from '../form/CInputGroup'
import { CInputGroupText } from '../form/CInputGroupText'

import { CTimePickerRollCol } from './CTimePickerRollCol'
import { CPicker, CPickerProps } from './../picker/CPicker'
import { convert12hTo24h, convert24hTo12h, getAmPm, isAmPm } from './CTimePicker.utils'

export interface CTimePickerProps
  extends Omit<
    CPickerProps,
    'autoClose' | 'component' | 'dark' | 'placeholder' | 'variant' | 'onChange'
  > {
  /**
   * A string of all className you want applied to the component.
   */
  className?: string
  /**
   * Enables selection cleaner element.
   */
  cleaner?: boolean
  /**
   * Cleaner icon.
   */
  cleanerIcon?: ReactNode | boolean
  /**
   * Default input icon.
   */
  defaultIcon?: ReactNode | boolean
  /**
   * Toggle the readonly state for the component.
   */
  inputReadOnly?: boolean
  /**
   * Sets the default locale for components. If not set, it is inherited from the navigator.language.
   */
  locale?: string
  /**
   * Callback fired when the user hovers over the calendar cell.
   */
  onChange?: (timeString: string, localeTimeString: string, date: Date) => void
  /**
   * Specifies a short hint that is visible in the input.
   */
  placeholder?: string
  /**
   * Size the component small or large.
   */
  size?: 'sm' | 'lg'
  /**
   * Default value of the component
   */
  value?: Date | string | null
  /**
   * Set the time picker variant to a roll or select.
   */
  variant?: 'roll' | 'select'
}

export const CTimePicker = forwardRef<HTMLDivElement | HTMLLIElement, CTimePickerProps>(
  (
    {
      className,
      cleaner = true,
      cleanerIcon,
      container = 'dropdown',
      defaultIcon,
      disabled,
      footer = true,
      inputReadOnly,
      locale = typeof navigator === 'undefined' ? 'en-US' : navigator.language,
      onChange,
      placeholder = 'Select time',
      size,
      value,
      variant = 'roll',
      ...rest
    },
    ref,
  ) => {
    const inputRef = useRef<HTMLInputElement>(null)
    const [date, setDate] = useState<Date | null>(
      value ? (value instanceof Date ? new Date(value) : new Date(`1970-01-01 ${value}`)) : null,
    )
    const [_ampm, setAmPm] = useState<'am' | 'pm'>(date ? getAmPm(new Date(date), locale) : 'am')

    useEffect(() => {
      setDate(
        value ? (value instanceof Date ? new Date(value) : new Date(`1970-01-01 ${value}`)) : null,
      )
    }, [value])

    useEffect(() => {
      if (inputRef.current) {
        inputRef.current.value = date ? date.toLocaleTimeString(locale) : ''
      }
    }, [date])

    const getHours = (locale: string) =>
      Array.from(new Array(isAmPm(locale) ? 12 : 24), (_, i) => {
        return {
          value: isAmPm(locale) ? i + 1 : i,
          label: (isAmPm(locale) ? i + 1 : i).toLocaleString(locale),
        }
      })

    const getMinutesOrSeconds = (locale: string) =>
      Array.from(new Array(60), (_, i) => {
        return {
          value: i,
          label: i.toLocaleString(locale).padStart(2, (0).toLocaleString(locale)),
        }
      })

    const selectedHour = date
      ? isAmPm(locale)
        ? convert24hTo12h(date.getHours())
        : date.getHours()
      : null

    const selectedMinutes = date ? date.getMinutes() : null

    const selectedSeconds = date ? date.getSeconds() : null

    const handleClear = (event: React.MouseEvent<HTMLElement>) => {
      event.stopPropagation()
      setDate(null)
    }

    const handleTimeChange = (set: 'hours' | 'minutes' | 'seconds' | 'toggle', value: string) => {
      const _date = date || new Date('1970-01-01')

      if (set === 'toggle') {
        if (value === 'am') {
          setAmPm('am')
          _date.setHours(_date.getHours() - 12)
        }
        if (value === 'pm') {
          setAmPm('pm')
          _date.setHours(_date.getHours() + 12)
        }
      }

      if (set === 'hours') {
        if (isAmPm(locale)) {
          _date.setHours(convert12hTo24h(_ampm, parseInt(value)))
        } else {
          _date.setHours(parseInt(value))
        }
      }

      if (set === 'minutes') {
        _date.setMinutes(parseInt(value))
      }

      if (set === 'seconds') {
        _date.setSeconds(parseInt(value))
      }

      setDate(new Date(_date))
      onChange && onChange(_date.toTimeString(), _date.toLocaleTimeString(), _date)
    }

    const InputGroup = () => (
      <CInputGroup className="picker-input-group" size={size}>
        <CFormInput
          disabled={disabled}
          placeholder={placeholder}
          readOnly={inputReadOnly}
          ref={inputRef}
        />
        {(defaultIcon !== false || cleaner) && (
          <CInputGroupText>
            {defaultIcon !== false && (
              <span className="picker-input-group-indicator">
                {defaultIcon ? (
                  defaultIcon
                ) : (
                  <span className="picker-input-group-icon time-picker-input-icon" />
                )}
              </span>
            )}
            {cleaner && date && (
              <span
                className="picker-input-group-cleaner"
                role="button"
                onClick={(event) => handleClear(event)}
              >
                {cleanerIcon ? (
                  cleanerIcon
                ) : (
                  <span className="picker-input-group-icon time-picker-cleaner-icon" />
                )}
              </span>
            )}
          </CInputGroupText>
        )}
      </CInputGroup>
    )

    const TimePickerSelect = () => {
      return (
        <>
          <span className="time-picker-inline-icon" />
          <CFormSelect
            disabled={disabled}
            options={getHours(locale).map((option) => {
              return {
                value: option.value.toString(),
                label: option.label,
              }
            })}
            onChange={(event: React.ChangeEvent<HTMLSelectElement>) =>
              handleTimeChange('hours', event.target.value)
            }
            value={selectedHour ? selectedHour : '0'}
          />
          <span className="px-1">:</span>
          <CFormSelect
            disabled={disabled}
            options={Array.from(new Array(60), (_, i) => {
              return {
                value: i.toString(),
                label: i.toLocaleString(locale).padStart(2, (0).toLocaleString(locale)),
              }
            })}
            onChange={(event: React.ChangeEvent<HTMLSelectElement>) =>
              handleTimeChange('minutes', event.target.value)
            }
            value={selectedMinutes ? selectedMinutes : '0'}
          />
          <span className="px-1">:</span>
          <CFormSelect
            disabled={disabled}
            options={Array.from(new Array(60), (_, i) => {
              return {
                value: i.toString(),
                label: i.toLocaleString(locale).padStart(2, (0).toLocaleString(locale)),
              }
            })}
            onChange={(event: React.ChangeEvent<HTMLSelectElement>) =>
              handleTimeChange('seconds', event.target.value)
            }
            value={selectedSeconds ? selectedSeconds : '0'}
          />
          {isAmPm(locale) && (
            <CFormSelect
              disabled={disabled}
              options={[
                { value: 'am', label: 'AM' },
                { value: 'pm', label: 'PM' },
              ]}
              onChange={(event: React.ChangeEvent<HTMLSelectElement>) =>
                handleTimeChange('toggle', event.target.value)
              }
              value={_ampm}
            />
          )}
        </>
      )
    }

    const TimePickerRoll = () => (
      <>
        <CTimePickerRollCol
          elements={getHours(locale)}
          onClick={(index: number) => handleTimeChange('hours', index.toString())}
          selected={selectedHour}
        />
        <CTimePickerRollCol
          elements={getMinutesOrSeconds(locale)}
          selected={selectedMinutes}
          onClick={(index: number) => handleTimeChange('minutes', index.toString())}
        />
        <CTimePickerRollCol
          elements={getMinutesOrSeconds(locale)}
          selected={selectedSeconds}
          onClick={(index: number) => handleTimeChange('seconds', index.toString())}
        />
        {isAmPm(locale) && (
          <CTimePickerRollCol
            elements={[
              { value: 'am', label: 'AM' },
              { value: 'pm', label: 'PM' },
            ]}
            selected={_ampm}
            onClick={(value: string) => handleTimeChange('toggle', value)}
          />
        )}
      </>
    )

    const _className = classNames('time-picker', className)

    return (
      <CPicker
        className={_className}
        container={container}
        disabled={disabled}
        footer={footer}
        onCancelClick={() => {
          setDate(
            value
              ? value instanceof Date
                ? new Date(value)
                : new Date(`1970-01-01 ${value}`)
              : new Date(),
          )
        }}
        toggler={InputGroup()}
        {...rest}
        ref={ref}
      >
        <div
          className={classNames('time-picker-body', {
            ['time-picker-roll']: variant === 'roll',
          })}
        >
          {variant === 'select' ? <TimePickerSelect /> : TimePickerRoll()}
        </div>
      </CPicker>
    )
  },
)

CTimePicker.propTypes = {
  ...CPicker.propTypes,
  className: PropTypes.string,
  locale: PropTypes.string,
  onChange: PropTypes.func,
  value: PropTypes.oneOfType([PropTypes.instanceOf(Date), PropTypes.string]),
  variant: PropTypes.oneOf(['roll', 'select']),
}

CTimePicker.displayName = 'CTimePicker'
