import React, { forwardRef, ReactNode, useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import { CButton } from '../button/CButton'
import { CDropdown, CDropdownProps } from '../dropdown/CDropdown'
import { CDropdownMenu } from '../dropdown/CDropdownMenu'
import { CDropdownToggle } from '../dropdown/CDropdownToggle'

export interface CPickerProps extends Omit<CDropdownProps, 'variant'> {
  /**
   * Cancel button text.
   */
  cancelButtonText?: ReactNode
  /**
   * Set container type for the component.
   */
  container?: 'dropdown' | 'inline'
  /**
   * Toggle the disabled state for the component.
   */
  disabled?: boolean
  /**
   * Toggle visibility of footer element or set the content of footer.
   */
  footer?: boolean | ReactNode
  /**
   * Ok button text
   */
  okButtonText?: ReactNode
  /**
   * Callback fired when the cancel button has been clicked.
   */
  onCancelClick?: () => void
  /**
   * Callback fired when the ok button has been clicked.
   */
  onOkClick?: () => void
  /**
   * The content of toggler.
   */
  toggler?: ReactNode
}

export const CPicker = forwardRef<HTMLDivElement | HTMLLIElement, CPickerProps>(
  (
    {
      children,
      cancelButtonText = 'Cancel',
      className,
      container = 'dropdown',
      disabled,
      footer,
      okButtonText = 'OK',
      onCancelClick,
      onOkClick,
      onHide,
      onShow,
      toggler: Toggler,
      visible,
      ...rest
    },
    ref,
  ) => {
    const [_visible, setVisible] = useState(visible)

    useEffect(() => {
      setVisible(visible)
    }, [visible])

    const Footer = () => (
      <>
        <CButton
          color="primary"
          size="sm"
          variant="ghost"
          onClick={() => onCancelClick && onCancelClick()}
        >
          {cancelButtonText}
        </CButton>
        <CButton
          color="primary"
          size="sm"
          onClick={() => {
            onOkClick && onOkClick()
            setVisible(false)
          }}
        >
          {okButtonText}
        </CButton>
      </>
    )

    const _className = classNames('picker', className)

    switch (container) {
      case 'inline':
        return <div className={_className}>{children}</div>
      default:
        return (
          <CDropdown
            autoClose="outside"
            className={_className}
            onHide={() => {
              setVisible(false)
              onHide && onHide()
            }}
            onShow={() => {
              setVisible(true)
              onShow && onShow()
            }}
            variant="dropdown"
            visible={_visible}
            {...rest}
            ref={ref}
          >
            <CDropdownToggle custom disabled={_visible || disabled}>
              {Toggler}
            </CDropdownToggle>
            <CDropdownMenu component="div">
              {children}
              {footer && (
                <div className="picker-footer">
                  {typeof footer === 'boolean' ? Footer() : footer}
                </div>
              )}
            </CDropdownMenu>
          </CDropdown>
        )
    }
  },
)

CPicker.displayName = 'CPicker'

CPicker.propTypes = {
  ...CDropdown.propTypes,
  container: PropTypes.oneOf(['dropdown', 'inline']),
  disabled: PropTypes.bool,
  onCancelClick: PropTypes.func,
  onOkClick: PropTypes.func,
}
